@import "globals.scss";

.register {
  .container {
    min-height: 100vh;
    height: 100vh;
    display: grid;
    place-items: center;
    form {
      > h2 {
        font-family: "Montserrat", sans-serif;
      }
      max-width: 500px;
      width: 450px;
      .mb-3 {
        label,
        input {
          font-family: "Poppins", sans-serif;
        }
      }
      .submit-btn {
        font-family: "Poppins", sans-serif;
      }
    }
    .disabled {
      .submit-btn {
        opacity: 0.6;
        pointer-events: none;
        cursor: not-allowed;
        box-shadow: none !important;
      }
    }
  }
}

.register_dark {
  .container {
    form {
      > h2 {
        color: $textHeading;
      }
      background-color: $darkGrey;
      .mb-3 {
        label,
        input {
          color: $textGrey;
        }
        input {
          background-color: $grey;
          border-color: $black;
        }
      }
    }
  }
}

.register_light {
  .container {
    form {
      > h2 {
        color: $darkGrey;
      }
      .mb-3 {
        label,
        input {
          color: $grey;
        }
      }
    }
  }
}

@media screen and (max-width: 512px) {
  .register {
    .container {
      // align-items: center;
      align-content: start;
      justify-items: center;

      form {
        width: 350px;
      }
    }
  }
}
